import React, { useState } from "react";
import PasswordInput from "../PasswordInput/PasswordInput";
import FeedbackInput from "../FeedBackInput/FeedBackInput";
// import "./FeedbackForm.scss";
import Btn from "../Btn/Btn";
import style from "./FeedbackForm.module.scss";

const FeedbackForm = () => {
  const [name, setName] = useState("");
  const [surname, setSurname] = useState("");
  const [tel, setTel] = useState("");
  const [mail, setMail] = useState("");
  const [value, setValue] = useState("");
  const [value1, setValue1] = useState("");

  const reset = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    setName("");
    setSurname("");
    setTel("");
    setMail("");
    setValue("");
    setValue1("");
  };
  const submitHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    // –ü—Ä–æ–≤–µ—Ä—è—é –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ—Å—Ç—å –ø–∞—Ä–æ–ª–µ–π
    if (value === value1 && value !== "" && value1 !== "") {
      // –í—ã–≤–æ–¥–∏–º –≤ –∫–æ–Ω—Å–æ–ª—å –æ–±—ä–µ–∫—Ç —Å –∏–Ω—Ç–µ—Ä–µ—Å—É—é—â–µ–π –Ω–∞—Å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π, –∫–æ—Ç–æ—Ä—É—é –æ—Ç–ø—Ä–∞–≤–∏–ª –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å
      const userSendInfo = {
        userName: name,
        userSurname: surname,
        userTel: tel,
        userMail: mail,
        password: value,
        doublePassword: value1,
      };
      console.log(userSendInfo);
      // eslint-disable-next-line
      alert("–î–∞–Ω–Ω—ã–µ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã!");
    } else if (value === "" || value1 === "") {
      // eslint-disable-next-line
      alert("–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å, –Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ –ø–æ–ª–µ –≤–≤–æ–¥–∞!");
    } else {
      setName("");
      // eslint-disable-next-line
      alert("–í–≤–µ–¥–µ–Ω–Ω—ã–µ –ø–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç!");
      setValue("");
      setValue1("");
    }
  };
  return (
    <>
      <h2 className={style.title}>
        –û—Å—Ç–∞–≤—å—Ç–µ —Å–≤–æ–∏ –∫–æ–Ω—Ç–∞–∫—Ç—ã –∏ –º—ã —Å–≤—è–∂–µ–º—Å—è —Å –í–∞–º–∏!
      </h2>
      <form className={style.form} action="#">
        <FeedbackInput
          label="–ò–º—è"
          type="text"
          placeholder="–ò–≤–∞–Ω"
          setValue={setName}
          value={name}
        />
        <FeedbackInput
          label="–§–∞–º–∏–ª–∏—è"
          type="text"
          placeholder="–ò–≤–∞–Ω–æ–≤"
          setValue={setSurname}
          value={surname}
        />
        <FeedbackInput
          label="–¢–µ–ª–µ—Ñ–æ–Ω"
          type="tel"
          placeholder="+375293333333"
          setValue={setTel}
          value={tel}
        />
        <FeedbackInput
          label="e-mail"
          type="email"
          placeholder="ivanov@yandex.by"
          setValue={setMail}
          value={mail}
        />
        <PasswordInput
          label="–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
          value={value}
          setValue={setValue}
        />
        <PasswordInput
          label="–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
          value={value1}
          setValue={setValue1}
        />
        <Btn onClick={submitHandler} btnName="–û—Ç–ø—Ä–∞–≤–∏—Ç—å –¥–∞–Ω–Ω—ã–µ" />
        <Btn onClick={reset} btnName="–û—á–∏—Å—Ç–∏—Ç—å —Ñ–æ—Ä–º—É" />
        {/* <button onClick={reset} type="button">
          Reset
        </button> */}
      </form>
    </>
  );
};
export default FeedbackForm;
